.login {
  width: 100%;
  height: 100vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  &-container {
    width: 100%;
    max-width: 350px;
    padding: 30px;
    padding-top: 10px;
    background-color: white;
    margin: 0 auto;
    border-radius: 10px;

    @media (max-width: 600px) {
      width: 100%;
      padding: 10px;
    }
    &_button {
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.5px;
      text-align: center;
      width: 100%;
      margin-top: 20px;
      padding: 10px 30px;
      cursor: pointer;
      background: #000;
      color: #fff;
      border: 1px solid #000;
    }
    h1 {
      color: black;
      text-align: center;
      margin: 5px 0 20px 0;
    }
    &_logo {
      width: 100%;
      img {
        margin: 0 auto;
        display: block;
        width: 100%;
        max-width: 100px;
        border-radius: 50%;
      }
    }
    &_input {
      background-color: #ffffff;
      border-radius: 10px;
      //   box-shadow: 0 2px 18px 0 rgba(0, 0, 0, 0.2);
      border: 1px solid #707070;
      color: #707070;
      font-size: 14px;
      font-weight: 300;
      text-align: left;
      padding: 10px;
      width: 100%;
      outline: none;

      &::placeholder {
        color: #707070cb;
      }
      &:first-of-type {
        margin-bottom: 20px;
      }
    }
  }
}
