.other-orders {
  width: 100%;
  height: 100%;
  &-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    // flex-direction: column;
  }
  &_title {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    @media (max-width: 600px) {
      flex-direction: column;
      justify-content: center;
    }
    button {
      width: 150px;
      margin: 0;
      position: absolute;
      margin-top: 20px;
      @media (max-width: 600px) {
        position: relative;
        width: 100%;
      }
    }
    h1 {
      font-size: 30px;
      margin: 0 auto;
    }
  }
}
