@import "../../utils.scss";
.nav {
  width: 230px;
  position: fixed;
  height: 100%;
  width: 230px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  background-size: cover;
  background-position: center center;
  display: block;
  z-index: 10;
  //   box-shadow: 0px 0px 45px 0px rgba(0, 0, 0, 0.6);
  transition: 0.5s cubic-bezier(0.685, 0.0473, 0.346, 1);
  // background: #fe8c00; /* fallback for old browsers */
  // background: -webkit-linear-gradient(to bottom, #f83600, #fe8c00); /* Chrome 10-25, Safari 5.1-6 */
  // background: linear-gradient(to bottom, #f83600, #fe8c00); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  @media (max-width: 600px) {
    transform: translateX(-500px);
    transition: all 300ms ease-in-out;
  }

  &.nav-open-responsive {
    @media (max-width: 600px) {
      transform: translateX(0);
    }
  }

  &-info {
  }
  &-content {
    width: 100%;
    min-height: 100%;
    max-height: 100%;
    z-index: 4;
    position: relative;
    // background: linear-gradient(to bottom, #fa621c 0%, #ca4f15 100%);
    // background-color: $secondary-black;
    // background: rgb(255, 183, 52);
    background: black;

    padding: 10px;
    &-logo {
      padding-bottom: 10px;
      border-bottom: 1px solid rgba($color: #ffffff, $alpha: 0.5);
      .title {
        color: white;
        font-size: 3rem;
        text-align: center;
        transition: all 500ms ease-in-out;
        @media (max-width: 800px) {
          font-size: 2.4rem;
        }
      }
    }
    &-list {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;
      &--item {
        width: 100%;
        padding: 10px 0;
        cursor: pointer;
        border-radius: 10px;
        a {
          display: block;
          color: white;
          font-size: 2rem;
          text-align: center;
          transition: all 400ms ease-in-out;
        }
        &:hover {
          background-color: white;
          a {
            color: $primary-black;
          }
        }
        &.selected {
          background-color: white;
          a {
            color: $primary-black;
          }
        }

        &--logo {
        }
      }
    }
  }
}

.overlay {
  display: none;
  @media (max-width: 600px) {
    &.open {
      display: block;
      position: fixed;
      z-index: 0;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
}

.logout {
  margin-top: 50px;
  display: block;
  color: white;
  font-size: 2rem;
  text-align: center;
  transition: all 400ms ease-in-out;
  &:hover {
    background-color: black;
  }
}
