@import "../../utils.scss";

.table {
  width: 100%;
  font-size: 1.5rem;
  border-collapse: collapse;
  @media (max-width: 600px) {
    font-size: 1.2rem;
  }
  &_head {
    padding: 10px 0;
    &-tr {
      text-align: left;
      th {
        border-color: rgba(255, 255, 255, 0.1);
        padding: 12px 7px;
        vertical-align: middle;

        text-transform: uppercase;
        font-weight: 700;
        border: 0;
        color: black;
      }
    }
  }
  &_body {
    &-tr {
      border-top: 1px solid transparent;
      border-bottom: 1px solid transparent;
      border-color: rgba(255, 255, 255, 0.1);

      border-top: 1px solid black;
      border-bottom: 1px solid black;
      border-color: black;
      &.expanded {
        border-bottom: none;
      }
      td {
        padding: 12px 7px;
        vertical-align: middle;
        font-weight: 300;
        color: black;
      }
    }
    &-tr--expandible {
      border-bottom: 1px solid transparent;
      border-color: rgba(255, 255, 255, 0.1);
      td {
        padding: 5px 7px;
        h5 {
          font-weight: 300;
          color: black;
          margin-bottom: 15px;
          span {
            color: black;
          }
        }
        h6 {
          font-weight: 300;
          font-size: 13px;
          color: black;
          margin-bottom: 10px;
          span {
            color: black;
            font-weight: 600;
          }
        }
      }
      &__buy {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        .item {
          font-size: 12px;
          margin-bottom: 15px;
          text-transform: uppercase;
          color: black;

          span {
            display: inline-block;
            color: black;
          }
          &-name {
            min-width: 185px;
          }
          &-container {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: flex-start;
          }
        }
      }
    }
  }
}
