.stock-item {
  width: 100%;
  //   padding: 20px;
  background-color: white;
  border-radius: 10px;
  margin-bottom: 30px;
  -webkit-box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);
  &__header {
    width: 100%;
    display: flex;
    color: black;
    align-items: center;
    justify-content: space-around;
    @media (max-width: 600px) {
      flex-direction: column;
    }
    cursor: pointer;
    img {
      width: 100px;
    }
  }
  &__body {
    // background-color: grey;
    width: 100%;
    padding: 20px 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    &-container {
      display: flex;
      @media (max-width: 600px) {
        flex-direction: column;
      }
    }
    &--right,
    &--left {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      width: 50%;
      @media (max-width: 600px) {
        width: 100%;
      }
    }
    &-item {
      //   display: flex;
      width: 100%;
      color: black;
      //   align-items: center;
      //   justify-content: flex-start;
      padding-left: 30px;
      margin-bottom: 20px;
      h3 {
        margin-bottom: 5px;
      }
      @media (max-width: 600px) {
        padding: 10px;
        margin-bottom: 10px;
      }
    }
    &.hidden {
      opacity: 0;
      visibility: hidden;
      height: 0;
      padding: 0;
    }
    &.show {
      opacity: 1;
      visibility: visible;
      height: auto;
      padding: 20px 0;
    }
  }
  &_input {
    background-color: #ffffff;
    border-radius: 10px;
    // box-shadow: 0 2px 18px 0 rgba(0, 0, 0, 0.2);
    border: 1px solid #707070;
    color: #707070;
    font-size: 14px;
    font-weight: 300;
    padding: 10px;
    width: 100%;
    outline: none;
    text-align: center;
    max-width: 100%;
    &::placeholder {
      color: #707070cb;
      text-align: center;
    }
  }
}
.itemstock {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 20px;
  padding-right: 30px;
  h4 {
    font-size: 14px;
    margin-right: 20px;
    width: 30px;
  }
}

.buttons {
  display: flex;
  justify-content: space-around;
  align-items: center;
  @media (max-width: 600px) {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    button {
      width: 90% !important;
    }
  }
}
