.order-item {
  width: 100%;
  background-color: white;
  border-radius: 10px;
  margin-bottom: 30px;
  box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);
  color: black;
  &__header {
    width: 100%;
    display: flex;
    color: black;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;
    padding: 30px 0;
    @media (max-width: 600px) {
      flex-direction: column;
      padding: 10px 0;
    }
    h3 {
      width: 50px;
      @media (max-width: 600px) {
        width: 100%;
        text-align: center;
        margin-bottom: 10px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
    &-paid {
      color: green;
    }
    &-process {
      color: orange;
    }
  }
  &__body {
    width: 100%;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    &-container {
      display: flex;
      @media (max-width: 600px) {
        flex-direction: column;
      }
    }
    &--right,
    &--left {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      width: 50%;
      @media (max-width: 600px) {
        width: 100%;
      }
    }
    &--right {
      @media (max-width: 600px) {
        margin-bottom: 20px;
      }
    }
    &.hidden {
      opacity: 0;
      visibility: hidden;
      height: 0;
      padding: 0;
    }
    &.show {
      opacity: 1;
      visibility: visible;
      height: auto;
      padding: 0 20px;
      padding-bottom: 20px;
    }

    h3 {
      span {
        font-family: "Poppins", sans-serif;
      }
    }
  }
}

.order-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
  h3 {
    font-family: "Poppins", sans-serif;
  }
}

