.new-other-order {
  width: 100%;
  background-color: white;
  border-radius: 10px;
  margin-bottom: 30px;
  -webkit-box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);
  padding: 20px;
  position: relative;
  color: black;
  &_title {
    font-size: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid black;
    padding-bottom: 5px;
  }
  &_header {
    margin: 10px 0;
  }
  &-container {
    display: flex;
    @media (max-width: 600px) {
      flex-direction: column;
    }
  }
  &--right,
  &--left,
  &--center {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 33.3%;
    @media (max-width: 600px) {
      width: 100%;
    }
  }
  &-item {
    width: 90%;
    color: black;
    //   align-items: center;
    //   justify-content: flex-start;
    margin-bottom: 20px;
    @media (max-width: 600px) {
      width: 100%;
    }
  }

  &_input {
    background-color: #ffffff;
    border-radius: 10px;
    // box-shadow: 0 2px 18px 0 rgba(0, 0, 0, 0.2);
    border: 1px solid #707070;
    color: #707070;
    font-size: 14px;
    font-weight: 300;
    padding: 10px;
    width: 100%;
    outline: none;
    text-align: left;
    max-width: 100%;
    resize: none;

    &::placeholder {
      color: #707070cb;
      text-align: left;
    }
  }
}

.order-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
  align-items: center;
  h3 {
    font-family: "Poppins", sans-serif;
  }
}

.delete {
  color: red;
  font-size: 14px;
  cursor: pointer;
}

.checkbox {
  display: none;
  &-container {
  }
}

.check {
  width: 15px;
  height: 15px;
  display: block;
  cursor: pointer;
  border: 1px solid #000;
  background-color: #fff;
  margin-left: 10px;
  &.checked {
    background-color: black;
  }
}

.check-container {
  display: flex;
  margin-top: 10px;
}

.button-order {
  margin-top: auto;
}

.select-input {
  margin-top: 10px;
}

.close-order {
  width: 15px;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
