@font-face {
  font-family: "RobotoMono-Regular";
  src: url("./assets/RobotoMono-Regular.woff") format("woff"); /* Pretty Modern Browsers */
}
@font-face {
  font-family: "MonumentExtended-Ultrabold";
  src: url("./assets/MonumentExtended-Ultrabold.woff") format("woff"); /* Pretty Modern Browsers */
}
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap");
$primary-black: #dedede;
$secondary-black: #27293d;

:root {
  --font-family-sans-serif: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%; //1 rem = 10px; 10px/16px = 62.5%
}

body {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  background-color: $primary-black;

  color: white;
}

input {
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
}
h1,
h2,
h3 {
  font-family: "MonumentExtended-Ultrabold", sans-serif;
  letter-spacing: 1px;
}

.container {
  max-width: 1366px;
  margin: 0 auto;
}

.card {
  background: $secondary-black;
  border: 0;
  position: relative;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 30px;
  box-shadow: 0 1px 20px 0px rgba(0, 0, 0, 0.1);

  width: 100%;
  background-color: white;
  border-radius: 10px;
  margin-bottom: 30px;
  box-shadow: 0px 0px 20px -15px rgba(0, 0, 0, 0.75);

  &-header {
    padding: 15px 15px 0;
    border: 0;
  }
  &-body {
    padding: 15px;
  }
}

.error {
  color: red;
  text-align: center;
  font-size: 12px;
  margin-top: 10px;
}

.view-title {
  // text-align: left;
  // color: black;
  // margin: 10px 0 20px 0;
  // font-size: 30px;

  text-align: center;
  color: black;
  margin: 10px 0 20px 0;
  font-size: 30px;
  /* color: white; */
  background: white;
  border-radius: 10px;
  padding: 10px;
}
