.button {
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.5px;
  text-align: center;
  width: 100%;
  margin-top: 20px;
  padding: 10px 30px;
  cursor: pointer;
  background: #000;
  color: #fff;
  border: 1px solid #000;
  &.secondary {
    background: #fff;
    color: #000;
  }
  &.disabled {
    background-color: grey;
    color: white;
  }
}
